<template>
  <div>
    <div
      class="hello bg-blue-300"
      ref="chartdiv"
    />
    <gallery ref="gallery" />
  </div>
</template>

<script>
import * as am4core from "@amcharts/amcharts4/core";
import * as am4maps from "@amcharts/amcharts4/maps";
import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import countriesData from '../assets/countries.json';
import Gallery from './Gallery.vue';


export default {
  components: { Gallery },
  name: 'Map',
  computed: {
      cities() {
          return countriesData.countries.map((country) => {
              return country.cities || null;
          });
      }
  },
  data() {
      return {
          images: [],
      }
  },
  methods: {
      close() {
          console.log('close');
      },
  },
  mounted() {

    // Themes begin
    am4core.useTheme(am4themes_animated);

    /**
     * Define SVG path for target icon
     */
    var targetSVG = "M9,0C4.029,0,0,4.029,0,9s4.029,9,9,9s9-4.029,9-9S13.971,0,9,0z M9,15.93 c-3.83,0-6.93-3.1-6.93-6.93S5.17,2.07,9,2.07s6.93,3.1,6.93,6.93S12.83,15.93,9,15.93 M12.5,9c0,1.933-1.567,3.5-3.5,3.5S5.5,10.933,5.5,9S7.067,5.5,9,5.5 S12.5,7.067,12.5,9z";
    let chart = am4core.create(this.$refs.chartdiv, am4maps.MapChart);

    // Set map definition
    chart.geodata = am4geodata_worldLow;

    chart.projection = new am4maps.projections.Miller();

    // Not yet visited country series
    var notYetVisitedCountry = chart.series.push(new am4maps.MapPolygonSeries());

    notYetVisitedCountry.useGeodata = true;

    // Configure series
    var notYetVisitedTemplate = notYetVisitedCountry.mapPolygons.template;
    notYetVisitedTemplate.tooltipText = "{name}";
    notYetVisitedTemplate.fill = am4core.color(process.env.VUE_APP_MAP_COUNTRY_COLOR);
    notYetVisitedTemplate.strokeOpacity = 0.5;
    notYetVisitedTemplate.nonScalingStroke = true;

    // Visited country series
    var includedCountries = [];
    var excludedCountries = ['AQ'];
    var visitedCountry = chart.series.push(new am4maps.MapPolygonSeries());
    visitedCountry.useGeodata = true;

    countriesData.countries.forEach(country => {
        includedCountries.push(country.key);
        visitedCountry.include = includedCountries;
        excludedCountries.push(country.key);
    });

    var visitedCountryTemplate = visitedCountry.mapPolygons.template;
    visitedCountryTemplate.applyOnClones = true;
    visitedCountryTemplate.togglable = false;
    visitedCountryTemplate.tooltipText = "{name}";
    visitedCountryTemplate.fill = am4core.color(process.env.VUE_APP_MAP_COUNTRY_VISITED_COLOR);
    visitedCountryTemplate.strokeOpacity = 0.5;
    visitedCountryTemplate.nonScalingStroke = true;

    var lastSelected;
    visitedCountryTemplate.events.on("hit", function(ev) {
        if (lastSelected) {
            // This line serves multiple purposes:
            // 1. Clicking a country twice actually de-activates, the line below
            //    de-activates it in advance, so the toggle then re-activates, making it
            //    appear as if it was never de-activated to begin with.
            // 2. Previously activated countries should be de-activated.
            lastSelected.isActive = false;
        }
        ev.target.series.chart.zoomToMapObject(ev.target);
        if (lastSelected !== ev.target) {
            lastSelected = ev.target;
        }
    })

    // Selected State
    var ss = visitedCountryTemplate.states.create("active");
    ss.properties.fill = am4core.color(process.env.VUE_APP_MAP_COUNTRY_SELECTED_COLOR);


    // Hover state
    var hs = visitedCountryTemplate.states.create("hover");
    hs.properties.fill = am4core.color(process.env.VUE_APP_MAP_COUNTRY_HOVER_COLOR);

    notYetVisitedCountry.exclude = excludedCountries;

    // create capital markers
    var imageSeries = chart.series.push(new am4maps.MapImageSeries());

    // define template
    var imageSeriesTemplate = imageSeries.mapImages.template;
    var circle = imageSeriesTemplate.createChild(am4core.Sprite);
    circle.scale = 0.4;
    circle.fill = new am4core.InterfaceColorSet().getFor("alternativeBackground");
    circle.path = targetSVG;
    // what about scale...

    // set propertyfields
    imageSeriesTemplate.propertyFields.latitude = "latitude";
    imageSeriesTemplate.propertyFields.longitude = "longitude";

    imageSeriesTemplate.horizontalCenter = "middle";
    imageSeriesTemplate.verticalCenter = "middle";
    imageSeriesTemplate.align = "center";
    imageSeriesTemplate.valign = "middle";
    imageSeriesTemplate.width = 8;
    imageSeriesTemplate.height = 8;
    imageSeriesTemplate.nonScaling = true;
    imageSeriesTemplate.tooltipText = "{title}";
    imageSeriesTemplate.fill = am4core.color("#000");
    imageSeriesTemplate.background.fillOpacity = 0;
    imageSeriesTemplate.background.fill = am4core.color("#ffffff");
    imageSeriesTemplate.setStateOnChildren = true;
    imageSeriesTemplate.states.create("hover");

    imageSeries.data = this.cities;

    imageSeriesTemplate.events.on("hit", function(ev) {
            this.$refs.gallery.$viewerApi({
                images: ev.target.dataItem.dataContext.images,
            });
    }, this);
  },

  beforeUnmount() {
    if (this.chart) {
      this.chart.dispose();
    }
  }
}
</script>

